* {
  margin: 0;
  padding: 0;
  box-sizing: border-box
}

html {
  font-family: Arial, Helvetica, sans-serif
}

h2 {
  font-size: 1.1rem
}

h3 {
  font-size: .7rem;
  font-weight: 500;
  color: #a3a3a3
}

ul {
  list-style-type: none;
  margin-top: 10px;
}

li {
  padding-left: 20px;
  transition: 0.5s;
  cursor: default;
}

li:hover {
  padding-left: 30px;
  background-color: #cee1ee;
}

.wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
}

.frame {
  margin: 20px;
  width: 50%;
  padding: 10px;
  background-color: #e2f3ff;
  border-radius: 5px
}

.bar-wrapper {
  width: 100%;
  text-align: center;
  margin-bottom: 20px
}

.bar-wrapper header {
  -webkit-justify-content: space-between;
  justify-content: space-between;
  padding-bottom: 10px
}

.bar, .bar-wrapper header {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
  flex-direction: row;
  -webkit-flex-wrap: nowrap;
  flex-wrap: nowrap;
  -webkit-align-items: center;
  align-items: center
}

.bar {
  -webkit-justify-content: flex-end;
  justify-content: flex-end;
  height: 40px
}

.bar-percent {
  position: relative;
  vertical-align: bottom;
  height: 100%
}

.bar-percent:hover {
  border: 2px solid #000
}

.left {
  text-align: left;
  left: 0
}

.left, .right {
  position: absolute;
  bottom: 0
}

.right {
  text-align: right;
  right: 0
}

.loading {
  z-index: 42;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  text-align: center;
  padding-top: 200px;
  background-color: #fff;
  color: #c2c2c2;
  font-size: 1rem
}

.content-available {
  opacity: 0;
  -webkit-transition: 1s;
  transition: 1s
}